import {useEffect, useContext} from 'react';
import { Link, Route, Switch, useRouteMatch } from 'react-router-dom';
import { useHistory } from 'react-router-dom';
import {useDispatch, useSelector} from 'react-redux';
import {LoadUser} from '../../application/middlewares/auth';
import {getIsLogged} from '../../application/selectors/auth';
import Input from "@material-tailwind/react/Input";
import Button from "@material-tailwind/react/Button";
import Checkbox from "@material-tailwind/react/Checkbox";
import { yupResolver } from '@hookform/resolvers/yup';
import * as Yup from 'yup';
import { useForm, Controller} from "react-hook-form";
import { post } from '../../application/middlewares';
import { 
    setToken
} from "../../application/actions/auth";
import logo1 from '../image/logo1.svg'
import fon_gamysh from '../image/fon_gamysh.svg'
import Lang1 from '../Language/Lang1';
import { LangContext } from "../Language/LangContext";


function Register(){
  const { lang, dispatchLang } = useContext(LangContext);

    const {  control, handleSubmit, formState: { errors }, setError} = useForm({
        resolver: yupResolver(schema(lang))
    });
    const dispatch = useDispatch();
    const history = useHistory();
    // const isLogged = useSelector(getIsLogged);
    // useEffect(() => {
    //     if(isLogged === false){
    //         dispatch(LoadUser({history}));
    //     }
    //     // eslint-disable-next-line
    // }, [dispatch, isLogged]);
    const onSubmit = (data) => {
        // console.log(data);
        data.owner_id=parseFloat(data.owner_id);
        // console.log(data?.owner_id);
        dispatch(post({
            url:'user/registration',
            token:'',
            data,
            action:(response)=>{
                if(response.success){
                    console.log("gelen register",response.data);
                    dispatch(setToken(response.data.access_token));
                    history.push('/auth/verifycode');
                }else {
                    console.log(response);
                    setError('phone', { type:'manual', message:response?.message?.data?.error?.phone});
                    
                }
            },

        }))
        // dispatch(AdminRegister({data, setError, history}));
    }

    // console.log(errors)
    return(
        <div className="flex justify-center items-center h-full w-full fon_gamysh"
        style={{ backgroundImage: `url(https://gamysh.com/uploads/images/fon_gamysh.svg)` }}
        
        >
        <div className="flex max-w-sm mx-auto overflow-hidden rounded-lg shadow-xl dark:bg-gray-800 lg:w-80">
        <form onSubmit={handleSubmit(onSubmit)} className="w-full px-6 py-4 md:px-8">
        <Link to='/' className='flex justify-center w-full  items-start'>
            <img src='https://gamysh.com/uploads/images/logo1.svg' className='auth_img' />
            </Link>
            <h2 className="text-2xl font-semibold text-center text-gray-700 dark:text-white">{Lang1[lang].regist}</h2>
            {/* <p className="text-xl text-center text-gray-600 dark:text-gray-200">Welcome back!</p> */}
            <div className="mt-6 w-full">
            <Controller
                    control={control}
                    name="full_name"
                    render={({ field: { onChange, onBlur, value, ref } }) => {
                        return (
                            <Input
                            onChange={onChange} onBlur={onBlur} 
                           type="text"  size="regular" color="blue"
                           outline={true} placeholder={Lang1[lang].full_name} error={errors.name?.message} className="back_white" />
                    )}}
                />
            </div>
            <div className="mt-6 w-full">
            <Controller
                    control={control}
                    name="email"
                    render={({ field: { onChange, onBlur, value, ref } }) => {
                        return (
                            <Input
                            onChange={onChange} onBlur={onBlur} 
                           type="email"  size="regular" color="blue"
                           outline={true} placeholder={Lang1[lang].email} error={errors.email?.message} />
                    )}}
                />
            </div>
            <div className="mt-6 w-full">
            <Controller
                    control={control}
                    name="phone"
                    render={({ field: { onChange, onBlur, value, ref } }) => {
                        return (
                            <Input
                            onChange={onChange} onBlur={onBlur} 
                           type="tel"  size="regular" color="blue"
                           outline={true} placeholder={Lang1[lang].phone} error={errors.phone?.message} />
                    )}}
                />
            </div>

            
            <div className="mt-6 w-full">
            <Controller
                    control={control}
                    name="password"
                    render={({ field: { onChange, onBlur, value, ref } }) => {
                        return (
                            <Input
                            onChange={onChange} onBlur={onBlur} 
                           type="password"  size="regular" color="blue"
                           outline={true} placeholder={Lang1[lang].password} error={errors.password?.message} />
                    )}}
                />
            </div>
            <div className="mt-6 w-full flex">
                <div>
                <Controller
                    control={control}
                    name="owner_id"
                    render={({ field: { onChange, onBlur, value, ref } }) => {
                        return (
                            <label className="flex items-center">
                            <input type="radio" name="owner_id" onChange={onChange} onBlur={onBlur} value={1} />
                            <p className="ml-2 text-sm">{Lang1[lang].owner}</p>
                            </label>
                    )}}
                />
                
                </div>
                <div className="ml-7">
                <Controller
                    control={control}
                    name="owner_id"
                    render={({ field: { onChange, onBlur, value, ref } }) => {
                        return (
                            <label className="flex items-center">
                            <input type="radio" name="owner_id" onChange={onChange} onBlur={onBlur} value={2} />
                            <p className="ml-2 text-sm">{Lang1[lang].rieltor}</p>
                            </label>
                    )}}
                />
                </div>
            </div>

            <div className="mt-4 w-full flex justify-between items-center">
            <div className="flex items-center justify-between">
                {/* <span className="border-b dark:border-gray-600 w-1/4"></span> */}

                <div className="text-xs w-4/6 text-gray-500 dark:text-gray-400 hover:underline text-center cursor-pointer">
                    <Link to='/auth/login'>
                    {Lang1[lang].already_have}
                    </Link>
                    </div>
                {/* <span className="border-b dark:border-gray-600 w-1/4"></span> */}
            </div>
            <Button
                color="lightBlue"
                buttonType="link"
                size="regular"
                rounded={false}
                block={false}
                iconOnly={false}
                ripple="dark"
                className="bg-blue-200"
            >
                {Lang1[lang].regist}
            </Button>
            </div>

            
            
            {/* <input type="radio" name="a" /> */}
            {/* <input type="radio" name="a" /> */}
        </form>
    </div>
    </div>
    )
}
const schema =(lang) => Yup.object().shape({
    phone:Yup.string().min(8, Lang1[lang].phone_min).max(8, Lang1[lang].phone_max)
      .required(Lang1[lang].required)
      .matches(
        /^(\+?\d{0,4})?\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{3}\)?)\s?-?\s?(\(?\d{4}\)?)?$/,
      Lang1[lang].only_number
    ),
    password: Yup.string().min(8, Lang1[lang].password_min).max(50, Lang1[lang].password_max).required(Lang1[lang].required).matches(
        /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,}$/,
      Lang1[lang].password_regex
    ),
    full_name: Yup.string().min(3, Lang1[lang].name_min).max(50, Lang1[lang].name_max).required(Lang1[lang].required)
  });

export default Register;